import React, { useMemo, useCallback } from 'react';
import { AgGridReact } from 'ag-grid-react';
import { ColDef, GridReadyEvent, RowClickedEvent } from 'ag-grid-community';
import styled from 'styled-components';
import { ProductData } from '../types';
import 'ag-grid-community/styles/ag-grid.css';
import 'ag-grid-community/styles/ag-theme-alpine.css';

const GridContainer = styled.div`
  height: 600px;
  width: 100%;
  
  .ag-theme-alpine {
    --ag-header-height: 44px;
    --ag-row-height: 44px;
    --ag-border-color: #e5e7eb;
    --ag-header-background-color: #f9fafb;
    --ag-odd-row-background-color: #ffffff;
    --ag-even-row-background-color: #f9fafb;
    --ag-font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
    --ag-font-size: 14px;
  }
  
  .ag-row {
    cursor: pointer;
  }
  
  .ag-row:hover {
    background-color: #f3f4f6 !important;
  }
  
  .status-blocked {
    background-color: #fef2f2 !important;
  }
  
  .status-incorrect {
    background-color: #fffbeb !important;
  }
  
  .status-missing {
    background-color: #f0fdf4 !important;
  }
  
  .status-healthy {
    background-color: #f0fdf4 !important;
  }
`;

const StatusBadge = styled.span<{ status: 'blocked' | 'incorrect' | 'missing' | 'healthy' }>`
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 500;
  background-color: ${props => {
    switch (props.status) {
      case 'blocked': return '#fef2f2';
      case 'incorrect': return '#fff7ed';
      case 'missing': return '#fffbeb';
      case 'healthy': return '#f0fdf4';
      default: return '#f3f4f6';
    }
  }};
  color: ${props => {
    switch (props.status) {
      case 'blocked': return '#dc2626';
      case 'incorrect': return '#ea580c';
      case 'missing': return '#d97706';
      case 'healthy': return '#16a34a';
      default: return '#374151';
    }
  }};
`;

const SourceBadge = styled.span<{ source: 'GDSN' | 'Vendor' }>`
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 500;
  background-color: ${props => props.source === 'GDSN' ? '#dbeafe' : '#f3e8ff'};
  color: ${props => props.source === 'GDSN' ? '#1e40af' : '#7c3aed'};
`;

const CostCell = styled.div`
  font-weight: 500;
  color: #059669;
`;

const SearchRelevanceBadge = styled.span<{ relevance: 'Head' | 'Body' | 'Tail' }>`
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 500;
  background-color: ${props => {
    switch (props.relevance) {
      case 'Head': return '#f0f9ff';
      case 'Body': return '#fefce8';
      case 'Tail': return '#fef2f2';
      default: return '#f3f4f6';
    }
  }};
  color: ${props => {
    switch (props.relevance) {
      case 'Head': return '#0369a1';
      case 'Body': return '#a16207';
      case 'Tail': return '#dc2626';
      default: return '#374151';
    }
  }};
`;

interface ProductGridProps {
  data: ProductData[];
  onRowClick: (product: ProductData) => void;
}

const ProductGrid: React.FC<ProductGridProps> = ({ data, onRowClick }) => {
  const statusCellRenderer = useCallback((params: any) => {
    const getStatusText = (status: string) => {
      switch (status) {
        case 'blocked': return 'Blocked';
        case 'incorrect': return 'Incorrect';
        case 'missing': return 'Missing';
        case 'healthy': return 'Healthy';
        default: return status;
      }
    };
    
    return <StatusBadge status={params.value}>{getStatusText(params.value)}</StatusBadge>;
  }, []);

  const sourceCellRenderer = useCallback((params: any) => {
    return <SourceBadge source={params.value}>{params.value}</SourceBadge>;
  }, []);

  const costCellRenderer = useCallback((params: any) => {
    return <CostCell>${params.value.toFixed(2)}</CostCell>;
  }, []);

  const productNameCellRenderer = useCallback((params: any) => {
    const maxLength = 60;
    const text = params.value;
    return text.length > maxLength ? `${text.substring(0, maxLength)}...` : text;
  }, []);

  const searchRelevanceCellRenderer = useCallback((params: any) => {
    return <SearchRelevanceBadge relevance={params.value}>{params.value}</SearchRelevanceBadge>;
  }, []);

  const columnDefs: ColDef[] = useMemo(() => [
    {
      headerName: '',
      field: 'selected',
      width: 120,
      checkboxSelection: true,
      headerCheckboxSelection: true,
      pinned: 'left',
      filter: 'agSetColumnFilter',
      filterParams: {
        values: ['true', 'false'],
        valueFormatter: (params: any) => params.value ? 'Selected' : 'Not Selected'
      }
    },
    {
      headerName: 'SKU',
      field: 'vendorCode',
      width: 300,
      filter: 'agTextColumnFilter',
      pinned: 'left',
      filterParams: {
        buttons: ['reset', 'apply'],
        closeOnApply: true
      }
    },
    {
      headerName: 'Status',
      field: 'status',
      width: 220,
      cellRenderer: statusCellRenderer,
      filter: 'agSetColumnFilter',
      filterParams: {
        values: ['blocked', 'incorrect', 'missing', 'healthy'],
        valueFormatter: (params: any) => {
          switch (params.value) {
            case 'blocked': return 'Blocked';
            case 'incorrect': return 'Incorrect';
            case 'missing': return 'Missing';
            case 'healthy': return 'Healthy';
            default: return params.value;
          }
        }
      }
    },
    {
      headerName: 'Source',
      field: 'source',
      width: 220,
      cellRenderer: sourceCellRenderer,
      filter: 'agSetColumnFilter',
      filterParams: {
        values: ['GDSN', 'Vendor']
      }
    },
    {
      headerName: 'Product Name',
      field: 'productName',
      width: 800,
      cellRenderer: productNameCellRenderer,
      filter: 'agTextColumnFilter',
      tooltipField: 'productName',
      filterParams: {
        buttons: ['reset', 'apply'],
        closeOnApply: true
      }
    },
    {
      headerName: 'ASIN',
      field: 'asin',
      width: 400,
      filter: 'agTextColumnFilter',
      filterParams: {
        buttons: ['reset', 'apply'],
        closeOnApply: true
      }
    },
    {
      headerName: 'External ID',
      field: 'externalId',
      width: 400,
      filter: 'agTextColumnFilter',
      filterParams: {
        buttons: ['reset', 'apply'],
        closeOnApply: true
      }
    },
    {
      headerName: 'PO Ordering Level',
      field: 'poOrderingLevel',
      width: 400,
      filter: 'agSetColumnFilter',
      filterParams: {
        values: ['MASTER_PACK', 'INNER_PACK', 'PALLET']
      }
    },
    {
      headerName: 'Cost',
      field: 'cost',
      width: 240,
      cellRenderer: costCellRenderer,
      filter: 'agNumberColumnFilter',
      type: 'numericColumn',
      filterParams: {
        buttons: ['reset', 'apply'],
        closeOnApply: true,
        allowedCharPattern: '\\d\\-\\.',
        numberParser: (text: string) => text == null ? null : parseFloat(text)
      }
    },
    {
      headerName: 'Qty',
      field: 'qty',
      width: 220,
      filter: 'agNumberColumnFilter',
      type: 'numericColumn',
      filterParams: {
        buttons: ['reset', 'apply'],
        closeOnApply: true,
        allowedCharPattern: '\\d\\-\\.',
        numberParser: (text: string) => text == null ? null : parseInt(text)
      }
    },
    {
      headerName: 'IPIP',
      field: 'ipip',
      width: 220,
      filter: 'agNumberColumnFilter',
      type: 'numericColumn',
      filterParams: {
        buttons: ['reset', 'apply'],
        closeOnApply: true,
        allowedCharPattern: '\\d\\-\\.',
        numberParser: (text: string) => text == null ? null : parseInt(text)
      }
    },
    {
      headerName: 'MOQ',
      field: 'moq',
      width: 220,
      filter: 'agNumberColumnFilter',
      type: 'numericColumn',
      filterParams: {
        buttons: ['reset', 'apply'],
        closeOnApply: true,
        allowedCharPattern: '\\d\\-\\.',
        numberParser: (text: string) => text == null ? null : parseInt(text)
      }
    },
    {
      headerName: 'MPPP',
      field: 'mppp',
      width: 220,
      filter: 'agNumberColumnFilter',
      type: 'numericColumn',
      filterParams: {
        buttons: ['reset', 'apply'],
        closeOnApply: true,
        allowedCharPattern: '\\d\\-\\.',
        numberParser: (text: string) => text == null ? null : parseInt(text)
      }
    },
    {
      headerName: 'Search Relevance',
      field: 'searchRelevance',
      width: 300,
      cellRenderer: searchRelevanceCellRenderer,
      filter: 'agSetColumnFilter',
      filterParams: {
        values: ['Head', 'Body', 'Tail']
      }
    }
  ], [statusCellRenderer, sourceCellRenderer, costCellRenderer, productNameCellRenderer, searchRelevanceCellRenderer]);

  const defaultColDef = useMemo(() => ({
    sortable: true,
    resizable: true,
    filter: true,
    floatingFilter: true
  }), []);

  const getRowClass = useCallback((params: any) => {
    return `status-${params.data.status}`;
  }, []);

  const onRowClicked = useCallback((event: RowClickedEvent) => {
    onRowClick(event.data);
  }, [onRowClick]);

  const onGridReady = useCallback((params: GridReadyEvent) => {
    params.api.sizeColumnsToFit();
  }, []);

  return (
    <GridContainer>
      <div className="ag-theme-alpine" style={{ height: '100%', width: '100%' }}>
        <AgGridReact
          rowData={data}
          columnDefs={columnDefs}
          defaultColDef={defaultColDef}
          onRowClicked={onRowClicked}
          onGridReady={onGridReady}
          getRowClass={getRowClass}
          rowSelection="multiple"
          suppressRowClickSelection={false}
          animateRows={true}
          pagination={true}
          paginationPageSize={15}
          domLayout="normal"
        />
      </div>
    </GridContainer>
  );
};

export default ProductGrid;
