interface SidePanelProps {
  isOpen: boolean;
  product: ProductData | null;
  onClose: () => void;
  onResolutionAction: (action: ResolutionAction) => void;
  onNavigate: (direction: 'prev' | 'next') => void;
  canNavigatePrev: boolean;
  canNavigateNext: boolean;
}

const SidePanel: React.FC<SidePanelProps> = ({
  isOpen,
  product,
  onClose,
  onResolutionAction,
  onNavigate,
  canNavigatePrev,
  canNavigateNext
}) => {
  const [customValues, setCustomValues] = useState<Record<string, string>>({});
  const [flagNotes, setFlagNotes] = useState('');

  const handleCustomValueChange = useCallback((field: string, value: string) => {
    setCustomValues(prev => ({ ...prev, [field]: value }));
  }, []);

  const handleUseVendor = useCallback((field: string) => {
    onResolutionAction({ type: 'use_vendor', field });
  }, [onResolutionAction]);

  const handleUseAmazon = useCallback((field: string) => {
    onResolutionAction({ type: 'use_amazon', field });
  }, [onResolutionAction]);

  const handleSubmitCustom = useCallback((field: string) => {
    const value = customValues[field];
    if (value) {
      onResolutionAction({ 
        type: 'submit_custom', 
        field, 
        value: isNaN(Number(value)) ? value : Number(value)
      });
      setCustomValues(prev => ({ ...prev, [field]: '' }));
    }
  }, [customValues, onResolutionAction]);

  const handleFlagForReview = useCallback(() => {
    if (flagNotes.trim()) {
      onResolutionAction({ 
        type: 'flag_review', 
        field: 'general', 
        notes: flagNotes.trim()
      });
      setFlagNotes('');
    }
  }, [flagNotes, onResolutionAction]);

  if (!product) return null;

  const hasExternalIdDiscrepancy = product.discrepancies?.externalId;
  const hasPackHierarchyDiscrepancy = product.discrepancies?.packHierarchy;

  return (
    <>
      <PanelOverlay isOpen={isOpen} onClick={onClose} />
      <PanelContainer isOpen={isOpen}>
        <PanelHeader>
          <HeaderTop>
            <HeaderLeft>
              <PanelTitle>SKU: {product.vendorCode}</PanelTitle>
              <ProductIds>
                <div>ASIN: {product.asin}</div>
                <div>External ID: {product.externalId}</div>
              </ProductIds>
            </HeaderLeft>
            <HeaderRight>
              <HeaderNavButton 
                disabled={!canNavigatePrev} 
                onClick={() => onNavigate('prev')}
              >
                ← Prev
              </HeaderNavButton>
              <HeaderNavButton 
                disabled={!canNavigateNext} 
                onClick={() => onNavigate('next')}
              >
                Next →
              </HeaderNavButton>
              <CloseButton onClick={onClose}>×</CloseButton>
            </HeaderRight>
          </HeaderTop>
          <ProductInfo>
            <StatusBadge status={product.status}>
              {product.status === 'blocked' ? '🛑 Blocked' : 
               product.status === 'incorrect' ? '🟠 Incorrect' : 
               product.status === 'missing' ? '⚪ Missing' : 
               product.status === 'healthy' ? '✅ Healthy' : product.status}
            </StatusBadge>
            <SourceBadge source={product.source}>{product.source}</SourceBadge>
          </ProductInfo>
        </PanelHeader>

        <PanelContent>
          {product.status === 'blocked' && (
            <AlertBanner>
              <AlertContent>
                <span style={{ fontSize: '16px' }}>⚠️</span>
                <AlertText>This attribute is blocking POs</AlertText>
              </AlertContent>
              <AlertButton onClick={() => window.open('/catalog-resolution', '_blank')}>
                Resolve in Catalog
              </AlertButton>
            </AlertBanner>
          )}

          {hasExternalIdDiscrepancy && (
            <Section>
              <SectionTitle>External ID Discrepancy</SectionTitle>
              <DiscrepancyCard>
                <DiscrepancyHeader>Product Identifier Mismatch</DiscrepancyHeader>
                <ComparisonRow>
                  <DataColumn>
                    <DataLabel>{product.source} Data</DataLabel>
                    <DataValue>{hasExternalIdDiscrepancy.vendorValue}</DataValue>
                  </DataColumn>
                  <DataColumn>
                    <DataLabel>Amazon PO Data</DataLabel>
                    <DataValue isConflict>{hasExternalIdDiscrepancy.amazonValue}</DataValue>
                  </DataColumn>
                  <ActionButtons>
                    <ActionButton 
                      variant="vendor" 
                      onClick={() => handleUseVendor('externalId')}
                    >
                      Use {product.source}
                    </ActionButton>
                    <ActionButton 
                      variant="amazon" 
                      onClick={() => handleUseAmazon('externalId')}
                    >
                      Use Amazon
                    </ActionButton>
                    <ActionButton 
                      variant="custom" 
                      onClick={() => handleSubmitCustom('externalId')}
                    >
                      Submit Custom
                    </ActionButton>
                  </ActionButtons>
                </ComparisonRow>
                <div style={{ padding: '0 16px 16px' }}>
                  <CustomValueInput
                    placeholder="Enter custom External ID..."
                    value={customValues.externalId || ''}
                    onChange={(e) => handleCustomValueChange('externalId', e.target.value)}
                  />
                </div>
              </DiscrepancyCard>
            </Section>
          )}

          {hasPackHierarchyDiscrepancy && (
            <Section>
              <SectionTitle>Pack Hierarchy Discrepancy</SectionTitle>
              <DiscrepancyCard>
                <DiscrepancyHeader>Items Per Inner Pack (IPIP)</DiscrepancyHeader>
                <ComparisonRow>
                  <DataColumn>
                    <DataLabel>{product.source} Data</DataLabel>
                    <DataValue>{hasPackHierarchyDiscrepancy.vendor.ipip}</DataValue>
                  </DataColumn>
                  <DataColumn>
                    <DataLabel>Amazon PO Data</DataLabel>
                    <DataValue isConflict>{hasPackHierarchyDiscrepancy.amazon.ipip}</DataValue>
                  </DataColumn>
                  <ActionButtons>
                    <ActionButton 
                      variant="vendor" 
                      onClick={() => handleUseVendor('ipip')}
                    >
                      Use {product.source}
                    </ActionButton>
                    <ActionButton 
                      variant="amazon" 
                      onClick={() => handleUseAmazon('ipip')}
                    >
                      Use Amazon
                    </ActionButton>
                    <ActionButton 
                      variant="custom" 
                      onClick={() => handleSubmitCustom('ipip')}
                    >
                      Submit Custom
                    </ActionButton>
                  </ActionButtons>
                </ComparisonRow>
                <div style={{ padding: '0 16px 16px' }}>
                  <CustomValueInput
                    type="number"
                    placeholder="Enter custom IPIP value..."
                    value={customValues.ipip || ''}
                    onChange={(e) => handleCustomValueChange('ipip', e.target.value)}
                  />
                </div>
              </DiscrepancyCard>

              <DiscrepancyCard>
                <DiscrepancyHeader>Inner Packs Per Master Pack (IPMP)</DiscrepancyHeader>
                <ComparisonRow>
                  <DataColumn>
                    <DataLabel>{product.source} Data</DataLabel>
                    <DataValue>{hasPackHierarchyDiscrepancy.vendor.ipmp}</DataValue>
                  </DataColumn>
                  <DataColumn>
                    <DataLabel>Amazon PO Data</DataLabel>
                    <DataValue isConflict>{hasPackHierarchyDiscrepancy.amazon.ipmp}</DataValue>
                  </DataColumn>
                  <ActionButtons>
                    <ActionButton 
                      variant="vendor" 
                      onClick={() => handleUseVendor('ipmp')}
                    >
                      Use {product.source}
                    </ActionButton>
                    <ActionButton 
                      variant="amazon" 
                      onClick={() => handleUseAmazon('ipmp')}
                    >
                      Use Amazon
                    </ActionButton>
                    <ActionButton 
                      variant="custom" 
                      onClick={() => handleSubmitCustom('ipmp')}
                    >
                      Submit Custom
                    </ActionButton>
                  </ActionButtons>
                </ComparisonRow>
                <div style={{ padding: '0 16px 16px' }}>
                  <CustomValueInput
                    type="number"
                    placeholder="Enter custom IPMP value..."
                    value={customValues.ipmp || ''}
                    onChange={(e) => handleCustomValueChange('ipmp', e.target.value)}
                  />
                </div>
              </DiscrepancyCard>

              <DiscrepancyCard>
                <DiscrepancyHeader>Master Packs Per Pallet (MPPP)</DiscrepancyHeader>
                <ComparisonRow>
                  <DataColumn>
                    <DataLabel>{product.source} Data</DataLabel>
                    <DataValue>{hasPackHierarchyDiscrepancy.vendor.mppp}</DataValue>
                  </DataColumn>
                  <DataColumn>
                    <DataLabel>Amazon PO Data</DataLabel>
                    <DataValue isConflict>{hasPackHierarchyDiscrepancy.amazon.mppp}</DataValue>
                  </DataColumn>
                  <ActionButtons>
                    <ActionButton 
                      variant="vendor" 
                      onClick={() => handleUseVendor('mppp')}
                    >
                      Use {product.source}
                    </ActionButton>
                    <ActionButton 
                      variant="amazon" 
                      onClick={() => handleUseAmazon('mppp')}
                    >
                      Use Amazon
                    </ActionButton>
                    <ActionButton 
                      variant="custom" 
                      onClick={() => handleSubmitCustom('mppp')}
                    >
                      Submit Custom
                    </ActionButton>
                  </ActionButtons>
                </ComparisonRow>
                <div style={{ padding: '0 16px 16px' }}>
                  <CustomValueInput
                    type="number"
                    placeholder="Enter custom MPPP value..."
                    value={customValues.mppp || ''}
                    onChange={(e) => handleCustomValueChange('mppp', e.target.value)}
                  />
                </div>
              </DiscrepancyCard>

              <DiscrepancyCard>
                <DiscrepancyHeader>Master Packs Per Layer (MPPL)</DiscrepancyHeader>
                <ComparisonRow>
                  <DataColumn>
                    <DataLabel>{product.source} Data</DataLabel>
                    <DataValue>{hasPackHierarchyDiscrepancy.vendor.mppl}</DataValue>
                  </DataColumn>
                  <DataColumn>
                    <DataLabel>Amazon PO Data</DataLabel>
                    <DataValue isConflict>{hasPackHierarchyDiscrepancy.amazon.mppl}</DataValue>
                  </DataColumn>
                  <ActionButtons>
                    <ActionButton 
                      variant="vendor" 
                      onClick={() => handleUseVendor('mppl')}
                    >
                      Use {product.source}
                    </ActionButton>
                    <ActionButton 
                      variant="amazon" 
                      onClick={() => handleUseAmazon('mppl')}
                    >
                      Use Amazon
                    </ActionButton>
                    <ActionButton 
                      variant="custom" 
                      onClick={() => handleSubmitCustom('mppl')}
                    >
                      Submit Custom
                    </ActionButton>
                  </ActionButtons>
                </ComparisonRow>
                <div style={{ padding: '0 16px 16px' }}>
                  <CustomValueInput
                    type="number"
                    placeholder="Enter custom MPPL value..."
                    value={customValues.mppl || ''}
                    onChange={(e) => handleCustomValueChange('mppl', e.target.value)}
                  />
                </div>
              </DiscrepancyCard>
            </Section>
          )}

          {product.discrepancies?.shippingAttributes && (
            <Section>
              <SectionTitle>Shipping Attributes</SectionTitle>
              <DiscrepancyCard>
                <DiscrepancyHeader>Master Pack Height</DiscrepancyHeader>
                <ComparisonRow>
                  <DataColumn>
                    <DataLabel>{product.source} Data</DataLabel>
                    <DataValue>{product.discrepancies.shippingAttributes.vendor.master_pack_height}</DataValue>
                  </DataColumn>
                  <DataColumn>
                    <DataLabel>Amazon PO Data</DataLabel>
                    <DataValue isConflict>{product.discrepancies.shippingAttributes.amazon.master_pack_height}</DataValue>
                  </DataColumn>
                  <ActionButtons>
                    <ActionButton 
                      variant="vendor" 
                      onClick={() => handleUseVendor('master_pack_height')}
                    >
                      Use {product.source}
                    </ActionButton>
                    <ActionButton 
                      variant="amazon" 
                      onClick={() => handleUseAmazon('master_pack_height')}
                    >
                      Use Amazon
                    </ActionButton>
                    <ActionButton 
                      variant="custom" 
                      onClick={() => handleSubmitCustom('master_pack_height')}
                    >
                      Submit Custom
                    </ActionButton>
                  </ActionButtons>
                </ComparisonRow>
                <div style={{ padding: '0 16px 16px' }}>
                  <CustomValueInput
                    type="number"
                    step="0.01"
                    placeholder="Enter custom height..."
                    value={customValues.master_pack_height || ''}
                    onChange={(e) => handleCustomValueChange('master_pack_height', e.target.value)}
                  />
                </div>
              </DiscrepancyCard>

              <DiscrepancyCard>
                <DiscrepancyHeader>Master Pack Width</DiscrepancyHeader>
                <ComparisonRow>
                  <DataColumn>
                    <DataLabel>{product.source} Data</DataLabel>
                    <DataValue>{product.discrepancies.shippingAttributes.vendor.master_pack_width}</DataValue>
                  </DataColumn>
                  <DataColumn>
                    <DataLabel>Amazon PO Data</DataLabel>
                    <DataValue isConflict>{product.discrepancies.shippingAttributes.amazon.master_pack_width}</DataValue>
                  </DataColumn>
                  <ActionButtons>
                    <ActionButton 
                      variant="vendor" 
                      onClick={() => handleUseVendor('master_pack_width')}
                    >
                      Use {product.source}
                    </ActionButton>
                    <ActionButton 
                      variant="amazon" 
                      onClick={() => handleUseAmazon('master_pack_width')}
                    >
                      Use Amazon
                    </ActionButton>
                    <ActionButton 
                      variant="custom" 
                      onClick={() => handleSubmitCustom('master_pack_width')}
                    >
                      Submit Custom
                    </ActionButton>
                  </ActionButtons>
                </ComparisonRow>
                <div style={{ padding: '0 16px 16px' }}>
                  <CustomValueInput
                    type="number"
                    step="0.01"
                    placeholder="Enter custom width..."
                    value={customValues.master_pack_width || ''}
                    onChange={(e) => handleCustomValueChange('master_pack_width', e.target.value)}
                  />
                </div>
              </DiscrepancyCard>

              <DiscrepancyCard>
                <DiscrepancyHeader>Master Pack Length</DiscrepancyHeader>
                <ComparisonRow>
                  <DataColumn>
                    <DataLabel>{product.source} Data</DataLabel>
                    <DataValue>{product.discrepancies.shippingAttributes.vendor.master_pack_length}</DataValue>
                  </DataColumn>
                  <DataColumn>
                    <DataLabel>Amazon PO Data</DataLabel>
                    <DataValue isConflict>{product.discrepancies.shippingAttributes.amazon.master_pack_length}</DataValue>
                  </DataColumn>
                  <ActionButtons>
                    <ActionButton 
                      variant="vendor" 
                      onClick={() => handleUseVendor('master_pack_length')}
                    >
                      Use {product.source}
                    </ActionButton>
                    <ActionButton 
                      variant="amazon" 
                      onClick={() => handleUseAmazon('master_pack_length')}
                    >
                      Use Amazon
                    </ActionButton>
                    <ActionButton 
                      variant="custom" 
                      onClick={() => handleSubmitCustom('master_pack_length')}
                    >
                      Submit Custom
                    </ActionButton>
                  </ActionButtons>
                </ComparisonRow>
                <div style={{ padding: '0 16px 16px' }}>
                  <CustomValueInput
                    type="number"
                    step="0.01"
                    placeholder="Enter custom length..."
                    value={customValues.master_pack_length || ''}
                    onChange={(e) => handleCustomValueChange('master_pack_length', e.target.value)}
                  />
                </div>
              </DiscrepancyCard>

              <DiscrepancyCard>
                <DiscrepancyHeader>Master Pack Weight</DiscrepancyHeader>
                <ComparisonRow>
                  <DataColumn>
                    <DataLabel>{product.source} Data</DataLabel>
                    <DataValue>{product.discrepancies.shippingAttributes.vendor.master_pack_weight}</DataValue>
                  </DataColumn>
                  <DataColumn>
                    <DataLabel>Amazon PO Data</DataLabel>
                    <DataValue isConflict>{product.discrepancies.shippingAttributes.amazon.master_pack_weight}</DataValue>
                  </DataColumn>
                  <ActionButtons>
                    <ActionButton 
                      variant="vendor" 
                      onClick={() => handleUseVendor('master_pack_weight')}
                    >
                      Use {product.source}
                    </ActionButton>
                    <ActionButton 
                      variant="amazon" 
                      onClick={() => handleUseAmazon('master_pack_weight')}
                    >
                      Use Amazon
                    </ActionButton>
                    <ActionButton 
                      variant="custom" 
                      onClick={() => handleSubmitCustom('master_pack_weight')}
                    >
                      Submit Custom
                    </ActionButton>
                  </ActionButtons>
                </ComparisonRow>
                <div style={{ padding: '0 16px 16px' }}>
                  <CustomValueInput
                    type="number"
                    step="0.01"
                    placeholder="Enter custom weight..."
                    value={customValues.master_pack_weight || ''}
                    onChange={(e) => handleCustomValueChange('master_pack_weight', e.target.value)}
                  />
                </div>
              </DiscrepancyCard>
            </Section>
          )}

          <FlagSection>
            <FlagTitle>Flag for Manual Review</FlagTitle>
            <p style={{ margin: '0 0 12px 0', fontSize: '14px', color: '#92400e' }}>
              If the discrepancy requires additional investigation or cannot be resolved automatically, 
              flag this item for manual review by the Supply Chain team.
            </p>
            <FlagTextarea
              placeholder="Describe the issue and any additional context..."
              value={flagNotes}
              onChange={(e) => setFlagNotes(e.target.value)}
            />
            <FlagButton onClick={handleFlagForReview}>
              Flag for Review
            </FlagButton>
          </FlagSection>
        </PanelContent>
      </PanelContainer>
    </>
  );
};

export default SidePanel;
