import React, { useState, useCallback, useMemo } from 'react';
import styled from 'styled-components';
import VendorCentralHeader from './components/VendorCentralHeader';
import KPICards from './components/KPICards';
import ProductGrid from './components/ProductGrid';
import SidePanel from './components/SidePanel';
import { ProductData, KPIData, ResolutionAction } from './types';
import { sampleProducts, kpiData } from './data/sampleData';

const AppContainer = styled.div`
  min-height: 100vh;
  background: #f9fafb;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
`;

const MainContent = styled.main`
  padding: 32px;
  max-width: 1400px;
  margin: 0 auto;
`;

const Section = styled.section`
  margin-bottom: 32px;
`;

const App: React.FC = () => {
  const [products, setProducts] = useState<ProductData[]>(sampleProducts);
  const [kpis, setKPIs] = useState<KPIData>(kpiData);
  const [selectedProduct, setSelectedProduct] = useState<ProductData | null>(null);
  const [isPanelOpen, setIsPanelOpen] = useState(false);
  const [cardFilter, setCardFilter] = useState<'blocked' | 'incorrect' | 'missing' | null>(null);

  // Filter products based on card selection
  const filteredProducts = useMemo(() => {
    if (!cardFilter) return products;
    return products.filter(product => product.status === cardFilter);
  }, [products, cardFilter]);

  // Get products with issues for navigation
  const productsWithIssues = useMemo(() => {
    return products.filter(product => product.status === 'blocked' || product.status === 'incorrect');
  }, [products]);

  // Get current product index in issues list
  const currentIssueIndex = useMemo(() => {
    if (!selectedProduct) return -1;
    return productsWithIssues.findIndex(product => product.id === selectedProduct.id);
  }, [selectedProduct, productsWithIssues]);

  const handleRowClick = useCallback((product: ProductData) => {
    setSelectedProduct(product);
    setIsPanelOpen(true);
  }, []);

  const handleCloseSidePanel = useCallback(() => {
    setIsPanelOpen(false);
    setSelectedProduct(null);
  }, []);

  const handleNavigateIssues = useCallback((direction: 'prev' | 'next') => {
    if (currentIssueIndex === -1) return;
    
    const newIndex = direction === 'prev' 
      ? Math.max(0, currentIssueIndex - 1)
      : Math.min(productsWithIssues.length - 1, currentIssueIndex + 1);
    
    setSelectedProduct(productsWithIssues[newIndex]);
  }, [currentIssueIndex, productsWithIssues]);

  const handleResolutionAction = useCallback((action: ResolutionAction) => {
    console.log('Resolution action:', action);
    
    // In a real application, this would make API calls to update the backend
    // For now, we'll just log the action and potentially update local state
    
    // Example: Update local state to reflect resolution
    if (selectedProduct && action.type !== 'flag_review') {
      setProducts(prevProducts => 
        prevProducts.map(product => 
          product.id === selectedProduct.id 
            ? { ...product, status: 'missing' as const }
            : product
        )
      );
      
      // Update KPIs
      setKPIs(prevKPIs => ({
        ...prevKPIs,
        activeDiscrepancies: Math.max(0, prevKPIs.activeDiscrepancies - 1)
      }));
    }
    
    // Show success message or handle error
    alert(`Resolution action processed: ${action.type} for ${action.field}`);
  }, [selectedProduct]);

  const handleCardFilterChange = useCallback((filter: 'blocked' | 'incorrect' | 'missing' | null) => {
    setCardFilter(filter);
  }, []);

  return (
    <AppContainer>
      <VendorCentralHeader />

      <MainContent>
        <Section>
          <KPICards 
            data={kpis} 
            selectedFilter={cardFilter}
            onFilterChange={handleCardFilterChange}
          />
        </Section>

        <Section>
          <ProductGrid 
            data={filteredProducts} 
            onRowClick={handleRowClick}
          />
        </Section>
      </MainContent>

      <SidePanel
        isOpen={isPanelOpen}
        product={selectedProduct}
        onClose={handleCloseSidePanel}
        onResolutionAction={handleResolutionAction}
        onNavigate={handleNavigateIssues}
        canNavigatePrev={currentIssueIndex > 0}
        canNavigateNext={currentIssueIndex < productsWithIssues.length - 1}
      />
    </AppContainer>
  );
};

export default App;
